@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&family=Montserrat:wght@400;500&family=Playfair+Display:ital,wght@0,400;1,400;1,500&family=Poppins:wght@300;400;500;600&display=swap');

*{
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root{
  --color-bg: #0a0a16;
  --color-bg-variant: #32323d;
  --color-primary: #000000;
  --color-primary-variant: rgba(238, 105, 10, 0.4);
  --color-white: #fff;
  --color-light: rgba(255,255,255,0.6);

  --transition: all 400ms ease; 

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}


html{
  scroll-behavior: smooth;
}

::-webkit-scrollbar{
  display: none;
}

body{
  font-family: 'Poppins', sans-serif;
  background: var(--color-bg);
  color: var(--color-white);
  line-height: 1.7;
  background-image: url(../src/assets/texture.jpg);
  background-position: center;
    background-size: cover;
    background-repeat: repeat;
    background-attachment: fixed;
}

.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5
{
  font-weight: 500;
}

h1{
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
  height: 100%;
}

section > h2, section >h5{
  text-align: center;
  color: var(--color-light);
}


section >h2 {
  color: var(--color-white);
  margin-bottom: 3rem;
}

.text-light{
  color: var(--color-light);
}

a{
  color: var(--color-white);
  transition: var(--transition);
}

a:hover{
  color: var(--color-white);
}

.btn {
  width: max-content;
  display: inline-block;
  color: var(--color-white);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

.btn:hover{
  background: var(--color-white);
  color: var(--color-primary);
  border-color: transparent;
}

.btn-primary{
  background: var(--color-primary);
  color: var(--color-white);
}

img{
  display: block;
  width: 100%;
  object-fit: cover;
}

@media screen and (max-width: 1024px){
  .container{
    width: var(--container-width-md);
  }

  section{
    margin-top: 6rem;
  }
}

@media screen and (max-width: 600px) {
  .container {
    width: var(--container-width-sm);
  }

  section {
    margin-top: 8rem;
  }
}